*{
    margin: 0;
    padding: 0;
    font-family: 'Times New Roman', Times, serif;
    box-sizing: border-box;
}
.freee{
    padding: 10px, 30px;
    width: 100%;
    background: #000735;
    display: flex;
}
.freee .icon{
    margin-top: 5px;
    margin-left: 10px;
    color: rgb(189, 189, 18);
    font-size: 32px;
}
.freee p{
    margin-left: 10px;
    margin-top: 8px;
    color: white;
    font-size: 20px;
    font-weight: 600;
}
.main_header{
    padding: 30px 40px;
    width: 100%;
    border-bottom: 1px solid #000735;
}
.main_header .container{
    display: flex;
    justify-content: space-between;
}
.main_header .container .search_box{
    margin-right: 30px;
}
.main_header .container .search_box input{
    outline: none;
    padding: 10px 30px;
    border: 3px solid #000735;
    width: 300px;
    color: aliceblue;
}
.main_header .container .search_box button{
    border: solid #000735;
    outline: none;
    padding: 10px 20px;
    cursor: pointer;
    background: #000735;
    color:white;
}
.main_header .container .icon{
    display: flex;
    margin-right: 20px;
}
.main_header .container .icon .account .user_icon{
    margin-right: 10px;
    font-size: 22px;
    color: #000735;
    cursor: pointer;
    margin-top: 1px;
}
.main_header .container .icon .account p{
    display: inline-flex;
    margin-right: 0px;
    margin-top: -5px;
    margin-left: 10px;
}
.main_header .container .icon .second_icon{
    display: inline-flex;
    margin-left: 30px;
    margin-top: 4px;
}
.main_header .container .icon .second_icon .link{
    
    margin-right: 10px;
    font-size: 22px;
    cursor: pointer;
    color:#000735
} 
.header{
    padding: 20px 30px;
    width: 100%;
    box-shadow: rgb(116, 114, 114) 0px 3px 10px;
    padding-bottom: 13px;
}
.header .container{
    justify-content: space-between;
    max-width: 100%;
    display: flex;
}
.header .container .nav ul{
    display: flex;
}
.header .container .nav ul li{
    margin-left: 20px;
    list-style: none;
}
.header .container .nav ul li .link{
    text-decoration: none;
    color:#000735;
    transition: 0.5s;
}
.header .container .nav ul li .link:hover{
    color:#000735;
}
.header .container .auth{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
}
.header .container .auth button{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
    outline: none;
    border: none;
    background: none;
}