*{
    margin: 0;
    padding: 0;
    font-family: 'Times New Roman', Times, serif;
    box-sizing: border-box;
}
.top_banner{
    padding: 30px 40px;
    width: 100%;
    height: 500px;
    background: #e3edf6;
    overflow: hidden;
}
.top_banner .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    
}
.top_banner .container .detail{
    margin-top: 9%;
    margin-left: 30px;
    text-decoration: none;
}
.top_banner .container .detail h2{
    font-size: 50px;
    color: #000735;
    /* margin-right: 40%; */
    margin-bottom: 20px;
}
.top_banner .container .detail .link{
    margin-top: 20px;
    margin-right: 77%;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 100px;
    color: white;
    background: #000735;
    transition: 0.5s;
    justify-content: space-between;
    text-decoration: none;
}
.top_banner .container .detail .link:hover{
    background: white;
    color: #000735;
}
.top_banner .container .img_box img{
    width: 800px;
    height: 550px;
    /* margin-bottom: -5.3%; */
    overflow: hidden;
    margin-left: 20%;
}
.product_type{
    padding: 10px 35px;
    width: 100%;
}
.product_type .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.product_type .container .box{
    padding: 10px 20px;
    margin-left: 10px;
}
.product_type .container .box .img_box{
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background: #e8f4ff;
    overflow: hidden;
    cursor: pointer;
}
.product_type .container .box .img_box img{
    align-items: center;
    max-width: 100%;
    margin-top: 0px;
    transition: 0.5s;
}
.product_type .container .box .img_box:hover img{
    transform: scale(1.2);
}
.product_type .container .box .detail{
    max-width: 100%;
    text-align: center;
    align-items: center;
}
.product_type .container .box .detail p{
    margin-top: 10px;
    color: #000735;
    
}
.about{
    padding: 40px 50px;
    width: 100%;
    margin-bottom: 10px;
}
.about .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.about .container .box{
     padding: 10px 20px;
     display: flex;
     background: #e3edf6;
     width: 250px;
     height: 80px;
     border-radius: 5px; 
     cursor: pointer; 
     margin-bottom: -80px;
}
.about .container .box .icon{
    color: #000735;
    font-size: 30px;
    margin-top: 13px;
    margin-right: 0px;
   
    
}

.about .container .box .detail{
    margin-top: 10px;
    margin-left: 10px;
}
.about .container .box .detail h3{
    color: #000735;
}
.about .container .box .detail p{
    color: #9a9a9a;
}
.product{
    padding: 30px 40px;
    width: 100%;
}
.product .container{
    display: flex;
    justify-content: space-between;
    flex-wrap:wrap;
}
.product h2{
     font-size: 32px;
     color: #000735;
     margin-bottom: 20px;
     margin-top: 20px;
}
.product .container .box{
    padding: 1px 1px;
    
    overflow:  hidden;
    width: 260px;
    height: 470px;
    
    margin-bottom: 10px;
    margin-left: 20px ;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 5px;
}
.product .container .box:hover{
    
    box-shadow: rgba(0, 0, 0, 0.35) 0px 3px 8px;
}
.product .container .box .img_box{
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    border-bottom: 1px solid #9a9a9a ;
}
.product .container .box .img_box img{
    width: 350px;
    height: 360px;
    overflow: hidden;
}
.product .container .box .img_box .icon{
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    right: -40px;
    transition: 0.5s;
}
.product .container .box .img_box:hover .icon{
    transform: translateX(-45px);
}

.product .container .box .img_box .icon li{
    list-style: none;
    padding: 10px 10px;
    border-bottom: 1px solid #9a9a9a;
    color: #000735;
    cursor: pointer;
    background: #000735;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 3px 8px;
    background: #fff;
    transition: 0.5s;
    font-size: 15px;
}
.product .container .box .img_box .icon li:hover{
    background:#000735;
    color: white; 
}
.product .container .box .detail{
    margin-top: 10px;
    margin-left: 10px;
    margin-left: 10px;
}
.product .container .box .detail p{
    color: #9a9a9a;
    font-size: 14px;
}
.product .container .box h3 {
    font-size: 20px;
    margin-top: 5px;
    color: black;
    transition: 0.5s;
}
.product .container .box:hover h3{
    color: #000735;
}
.product .container .box .detail h4{
    margin-top: 5px;
    font-size: 14px;
    color: #000735 ;
}

