*{
    margin:0;
    padding: 0;
    font-family: 'Times New Roman', Times, serif;
    box-sizing: border-box;
}
.footer{
    padding: 30px 40px;
    width: 100%;
    background: #ecf2f7;
}
.footer .container{
    max-width: 100%;
    display: flex;
    justify-content: space-between;
}
.footer .container .about{
    max-width: 30%;

}
.footer .container .about .logo{
    margin-top: -50px;
}
.footer .container .about .detail p{
    margin-top:20px;
    font-size: 16px;
    color: #000735;
}
.footer .container .about .detail .icon{
    margin-top: 20px;
    display: flex;  
    margin-left: -25px;  
}
.footer .container .about .detail .icon li{
    margin-left: 20px;
    list-style: none;
    padding: 10px 10px;
    box-shadow: rgba(0, 0, 0, 0.15)1.95px 1.95px 2.6px;
    cursor: pointer;
    transition: 0.3s;
}
.footer .container .about .detail .icon li:hover{
    background: #000735;
    color: white;
    border-radius: 5px;
    font-size: 16px;
}
.footer .container .account{
    margin-right: 90px;
    margin-top: 20px;
}
.footer .container .account h3{
    font-size: 32px;
    color: #000735;
}
.footer .container .account ul{
    margin-top: 10px;
    margin-left: 20px;
}
.footer .container .account ul li{
    margin-top: 5px;
    color: #000735;
    font-size: 15px;
    transition: 0.3s;
    cursor: pointer;
}
.footer .container .account ul li:hover{
    color: #000735;
    font-size: 20px;
}
.footer .container .page{
    margin-right: 150px;
    margin-top: 20px;
}
.footer .container .page h3{
    font-size: 32px;
    color: #000735;
}
.footer .container .page ul{
    margin-top: 10px;
    margin-left: 20px;
}
.footer .container .page ul li{
    margin-top: 5px;
    color: #000735;
    font-size: 15px;
    transition: 0.3s;
    cursor: pointer;
}
.footer .container .page ul li:hover{
    color: #000735;
    font-size: 20px;
}
